.container{
  padding: 0.2em;
}

.container.grid{
  width: 50%;
}

.container.list{
  width: 100%;
}

.video{
  display: flex;
  align-items: center;
  border: 1px solid #eee;
  box-shadow: 3px 3px 5px 0px rgba(191,191,191,0.53);
  cursor: pointer;
  transition: transform 250ms ease-in;
}

.video:hover{
  transform: scale(1.02);
}

.thumbnail{
  width: 40%;
  height: 100%;
}

.metadata{
  width: 60%;
  margin-left: 0.2em;
}

.title,
.channel{
  margin: 0;
  font-size: 1.1rem;
}

.channel{
  font-size: 0.8rem;
}